body {
  font-family: 'Chelsea Market', cursive;
  margin: 20;
  padding: 0;
  background-color: #f8f8f8;
}

/* Yellow box */
.yellow-box {
  background-color: #FFF031;
  border-radius: 20px;
  border: 2px solid white;
  padding: 20px;
  padding-left: 0;  /* Remove padding on the left */
  padding-right: 0;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding-top: 0; /* Allow grey box to sit at the top of the yellow box */
}

.yellow-box .space-y-8 > div:first-child {
  margin-top: 20px;
}

/* Heading box */
.heading-box {
  background-color: #454545;
  padding: 0;
  margin: 0;
  text-align: center;
  font-size: 1.6rem;
  color: white;
  border-radius: 20px 20px 0 0;
  border: 2px solid white;
  width: 100%;
}

/* Green box for results */
.green-box {
  background-color: #57B42D;
  color: white;
  padding: 20px;
  border-radius: 10px;
  border: 2px solid white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-right: 20px;
  display: flex;
  flex-direction: column; /* Ensures the button is aligned at the bottom */
  justify-content: space-between; /* Ensures that the button sticks to the bottom */
}

.container {
  border-radius: 20px;
  overflow: hidden;
  background: white;
}

.input-box,
.button {
  border-radius: 8px;
}

.text-center {
  text-align: center;
}

.grid-cols-1 {
  display: flex;
  justify-content: space-between;
}

.grid-cols-1 .green-box {
  margin-left: 20px;
  margin-top: 20px;
}

.yellow-box p {
  font-size: 1.2rem;
  color: black;
}

/* Blue Button Styles */
.button {
  background-color: #1427B8;  /* Blue background */
  color: white;
  font-size: 1.3rem;
  border-radius: 8px;
  cursor: pointer;
  border: 2px solid white;
  transition: background-color 0.3s;
  display: block; /* Ensure it fills the available space */
  margin: 0 auto; /* Centers the button horizontally */
  text-align: center; /* Center the text inside the button */
}

.button:hover {
  background-color: #4682b4;  /* Darker blue when hovered */
}

.button:focus {
  outline: none; /* Remove outline on focus */
}
.divider {
  border-bottom: 2px solid white;
  margin: 10px 0;
}
/* Ensure the slider takes up the same width as the input fields */
.green-box .slider {
  width: 100%; /* This will make the slider take the full width of its container */
  height: 8px; /* Adjust slider height if necessary */
}
/* Green box divider */
.green-box hr {
  border: 1px solid white;
  margin: 20px 0; /* Adjust the spacing between the dividers */
}
@keyframes wiggle {
  0% { transform: translateX(0); }
  25% { transform: translateX(-3px); }
  50% { transform: translateX(3px); }
  75% { transform: translateX(-3px); }
  100% { transform: translateX(0); }
}

.button {
  background-color: #1427B8;  /* Blue background */
  color: white;
  padding: 12px 24px;
  font-size: 1rem;
  border-radius: 8px;
  cursor: pointer;
  border: 2px solid white;
  transition: background-color 0.3s;
  animation: wiggle 1s infinite;
}

.button:hover {
  background-color: #4682b4;  /* Darker blue when hovered */
}

.button:focus {
  outline: none; /* Remove outline on focus */
}
.large-label {
  font-size: 1.8rem; /* You can adjust the size here */
}
